ul.simple-pagination {
  list-style: none;
}

.simple-pagination {
  display: block;
  overflow: hidden;
  padding: 0 5px 5px 0;
  margin: 0;
}

.simple-pagination ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.simple-pagination li {
  list-style: none;
  padding: 0;
  margin: 0;
  float: left;
}

span.ellipse.clickable {
  cursor: pointer;
}

.ellipse input {
  width: 3em;
}

/*------------------------------------*\
	Light Theme Styles
\*------------------------------------*/
.light-theme span {
  cursor: pointer;
}

.light-theme a,
.light-theme span {
  text-decoration: none;
  float: left;
  color: #666;
  font-size: 14px;
  line-height: 40px;
  font-weight: normal;
  text-align: center;
  min-width: 14px;
  padding: 0 7px;
  margin: 0 5px 0 0;
  border-radius: 3px;
  //box-shadow: 0 1px 2px rgba(0,0,0,0.2);
  background-color: #FFF;
  border-radius: 22px;
  width: 40px;
  height: 40px;
}

.light-theme a:not(.next):not(.prev):hover,
.light-theme li:not(.disabled):not(.active) span:not(.ellipse) span:hover {
  text-decoration: none;
  background: linear-gradient(var(--color-main-opacity, rgba(188, 156, 255, 0.2)), var(--color-main_grad-opacity, rgba(139, 164, 249, 0.2)));
}

.light-theme .current {
  background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
  border: none;
  color: #FFF;
  cursor: default;
}

.light-theme .ellipse {
  background: none;
  border: none;
  border-radius: 0;
  box-shadow: none;
  font-weight: bold;
  cursor: default;

  &:hover {
    cursor: default;
    //background: #fff;
  }
}

.light-theme .next, .light-theme .prev {
  position: relative;
  background: linear-gradient(180deg, #6FCF97 0%, #66D2EA 100%);

  &:hover {
    background: linear-gradient(var(--color-main-opacity, rgba(111, 207, 151, 0.5)), var(--color-main_grad-opacity, rgba(102, 210, 234, 0.5)));
  }

  svg {
    position: absolute;
    content: "";
    top: 11px;
    left: 11px;
    width: 18px;
    height: 18px;

    path {
      fill: #FFF;
    }
  }
}

.light-theme .prev svg{
  transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
  -moz-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  -o-transform: rotate(180deg);
}

.pagin-text > * {
  display: inline-block;
  height: 24px;
}
.page_number-dash{
  margin-left: 3px;
  margin-right: 3px;
}
.page_number-min, .page_number-max {
  width: auto;
  text-align: center;
}
.pagin-text .page_number-other{
  margin-left: 5px;
}